import React from "react";
import { motion } from "framer-motion";

const products = [
  { name: "Premium Wireless Headphones", price: "$299", img: "https://source.unsplash.com/200x200/?headphones" },
  { name: "Smart Fitness Watch", price: "$199", img: "https://source.unsplash.com/200x200/?smartwatch" },
  { name: "Organic Cotton T-Shirt", price: "$35", img: "https://source.unsplash.com/200x200/?tshirt" },
  { name: "Laptop Stand Adjustable", price: "$89", img: "https://source.unsplash.com/200x200/?laptop" },
  { name: "Ceramic Coffee Mug Set", price: "$45", img: "https://source.unsplash.com/200x200/?coffee" },
  { name: "Portable Bluetooth Speaker", price: "$79", img: "https://source.unsplash.com/200x200/?speaker" },
  { name: "LED Desk Lamp", price: "$65", img: "https://source.unsplash.com/200x200/?lamp" },
  { name: "Plant-Based Protein Powder", price: "$39", img: "https://source.unsplash.com/200x200/?protein" },
];

const FeaturedProducts = () => {
  return (
    <section className="bg-gray-100 py-12">
      <div className="max-w-7xl mx-auto">
        <h2 className="text-2xl font-semibold text-center mb-8">
          Featured Products
        </h2>
        <motion.div
          className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-6"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
          transition={{ staggerChildren: 0.2 }}
        >
          {products.map((prod, i) => (
            <motion.div
              key={i}
              className="bg-white shadow rounded-md p-4 hover:shadow-lg"
              variants={{
                hidden: { opacity: 0, y: 40 },
                visible: { opacity: 1, y: 0 },
              }}
              whileHover={{ scale: 1.05 }}
            >
              <img
                src={prod.img}
                alt={prod.name}
                className="w-full h-40 object-cover rounded-md mb-2"
              />
              <h3 className="font-semibold">{prod.name}</h3>
              <p className="text-blue-600 font-bold">{prod.price}</p>
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="mt-2 bg-blue-600 text-white px-3 py-1 rounded-md w-full"
              >
                Add to Cart
              </motion.button>
            </motion.div>
          ))}
        </motion.div>
        <div className="text-center mt-8">
          <motion.button
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
            className="px-4 py-2 bg-gray-800 text-white rounded-md"
          >
            View All Products
          </motion.button>
        </div>
      </div>
    </section>
  );
};

export default FeaturedProducts;
