import React from "react";
import { motion } from "framer-motion";

const categories = [
  { name: "Electronics", count: "2,450+ Items" },
  { name: "Fashion", count: "1,800+ Items" },
  { name: "Home & Garden", count: "3,200+ Items" },
  { name: "Sports & Fitness", count: "1,500+ Items" },
  { name: "Beauty & Care", count: "980+ Items" },
  { name: "Books & Media", count: "750+ Items" },
];

const Categories = () => {
  return (
    <section className="max-w-7xl mx-auto py-12">
      <h2 className="text-2xl font-semibold text-center mb-8">
        Shop by Category
      </h2>
      <motion.div
        className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-6"
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true }}
        transition={{ staggerChildren: 0.2 }}
      >
        {categories.map((cat, i) => (
          <motion.div
            key={i}
            className="bg-white shadow rounded-md p-4 text-center hover:shadow-lg cursor-pointer"
            variants={{
              hidden: { opacity: 0, y: 30 },
              visible: { opacity: 1, y: 0 },
            }}
          >
            <img
              src={`https://source.unsplash.com/200x200/?${cat.name}`}
              alt={cat.name}
              className="w-full h-32 object-cover rounded-md mb-2"
            />
            <h3 className="font-semibold">{cat.name}</h3>
            <p className="text-gray-500 text-sm">{cat.count}</p>
          </motion.div>
        ))}
      </motion.div>
    </section>
  );
};

export default Categories;
